<template>
    <section id="main-content" class="main-content">
        <loader :is-visible="isLoading"></loader>
        <section class="main-content">            
            <div class="left-menu-tab div-click-event">
                    <div class="col-md-12 p-0 ">
                        <div class="theme-primary partition">
                            <span class="p-name text-white textelipsis">{{itemTypeName}}</span>
                        </div>
                    </div>
                    <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                        <li class="nav-item">
                            <a class="nav-link left-menu" :class="{ 'active': activeKey == 1 }"
                                data-bs-target="#tab-lead-content" href="javascript:void(0)" data-bs-toggle="tab"
                                role="tab" id="tab-Details" v-on:click="loadData('timeline'); DisableButton()">
                                <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>
                                {{ $t('Detail') }}
                            </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" :class="{ 'active': activeKey == 2 }"
                                data-bs-target="#tab-notes-content" href="javascript:void(0)" id="tab-notes"
                                data-bs-toggle="tab" role="tab" v-on:click="loadData('notes')">
                                <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>
                                {{ $t('NOTE') }}
                            </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" :class="{ 'active': activeKey == 3 }"
                                data-bs-target="#tab-attachment-content" href="javascript:void(0)" id="tab-attachment"
                                data-bs-toggle="tab" role="tab" v-on:click="loadData('attachments')">
                                <em class="fa fa-file" aria-hidden="true" aria-selected="false"></em>
                                {{ $t('Attachments') }}
                            </a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link left-menu" data-toggle="tab" role="tab"
                                :class="{ 'active': activeKey == 4}" data-bs-target="#tab-tax-content"
                                href="javascript:void(0)" id="tab-tax" v-on:click="loadData('tax')"><em class="fa fa-money" aria-hidden="true" aria-selected="false"></em>{{ $t('Tax')}}
                            </a>
                        </li>
                    </ul>
            </div>
            <div class="right-content">
                    <div class="tab-pane mb-2">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-name text-white" id="span_ForSelectedTabName"></span>
                            <span class="p-actions float-right">
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }" title
                                    data-toggle-tooltip="tooltip" data-original-title="Card View"
                                    v-on:click="NoteListType = 'card'" v-if="isNotes && NoteListType == 'list'">
                                    <em class="fa fa-th-large"></em>
                                    <br />
                                    {{ $t('CardView') }}
                                </a>
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }" title
                                    data-toggle-tooltip="tooltip" data-original-title="List View"
                                    v-on:click="NoteListType = 'list'" v-if="isNotes && NoteListType != 'list'">
                                    <em class="fa fa-list"></em>
                                    <br />
                                    {{ $t('ListView') }}
                                </a>
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }" title
                                    id="ancaddnotesloaddata" data-toggle-tooltip="tooltip"
                                    data-original-title="Add Notes" v-on:click="openShowNotePopup()" v-if="isNotes">
                                    <em class="fa fa-plus"></em>
                                    <br />
                                    {{ $t('AddNotes') }}
                                </a>
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true }"
                                    :title="$t('UploadFile')" v-if="isAttachment"
                                    v-on:click="openCRMAttachmentModal(null);">
                                    <em class="fa fa-plus"></em>
                                    <br />
                                    {{ $t('Add') }} {{ $t('Attachment') }}
                                </a> <RouterLink to="/CRM/ItemTypeListing" class="p-action-btn text-white float-right">
                                    <em class="fa fa-angle-double-left"></em>
                                    <br />
                                    {{ $t('BacktoList') }}
                                </RouterLink> 
                                <RouterLink :to="'/Asset/Update/'+this.$route.params.id+'?moduleName='+this.$route.params.moduleName+'&subModuleCode='+this.$route.params.subModuleCode+'&reqfrom=/CRM/ItemTypeListing'" class="p-action-btn text-white">
                                    <em class="fa fa-pencil"></em>
                                    <br />{{ $t('Edit') }}
                                </RouterLink>                               
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns"><em    class="fa fa-angle-up text-white"></em></a>
                            </span>
                        </div>
                    </div>
                    <div class="tab-pane mb-0">
                        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                            <div class="expand-list">
                                <a href="javascript:;"
                                    class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                                </a>
                            </div>
                            <div class="tab-pane p-0 pb-2 border-0" role="tabpanel" id="tab-detail-content"
                                aria-labelledby="tab-detail-content" :class="{ 'active show': activeKey == 1 }">

                                <div class="listing">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{ $t('Details') }}</h2>
                                                    <div class="cont_right ">
                                                        <div class="bar">
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="card-body">
                                                    <div class="panel1" v-for="group in LeftViewData" :key="group">
                                                        <div class="heading-border">
                                                            <h5 class="h5 mb-2">
                                                                <span>{{GetLocalizedValue(group.group_name,'')}}</span>
                                                            </h5>
                                                        </div>
                                                        <div class="row">
                                                            <div class="col-md-6 col-lg-3"
                                                                v-for="(item, index) in group.LeftViewData" :key="index">
                                                                <div class="form-group">
                                                                    <label>
                                                                        <strong>{{GetLocalizedValue(item.name,
                                                                        item.display_name)}}:</strong>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">
                                                                            {{item.value | formatDate}}</span>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code == 'decimal'">{{isBlank(item.value)?"0.00":item.value | currency}}</span>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options != 'Lookup' && (item.name != 'description' || (item.name == 'description' && isBlank(item.value) != true))">{{
                                                                            isBlank(item.value)?"--":(item.value)}}</span>
                                                                        <crmreadmore callFrom="ProductTypeDetails"
                                                                            v-if="(item.name == 'description' || item.name == 'term_and_conditions') && !isBlank(item.value)"
                                                                            v-bind:data="item.value"
                                                                            :heading="$t('Description')" length="100" class="ml-1">
                                                                        </crmreadmore>
                                                                        
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">{{ isBlank(item.value)?"--":item.value}}</span>
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>Timeline</h2>
                                                </div>
                                                <div class="card-body">
                                                    <crmtimeline v-bind:timelinedata="timelinedata"></crmtimeline>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="tab-pane fade p-0 pb-2 border-0" id="tab-notes-content" role="tabpanel"
                                aria-labelledby="tab-notes-content" :class="{ 'active show': activeKey == 2 }">
                                <crmnote v-if="noteData" v-bind:notedata="noteData" :userid="userid"
                                    :companyid="companyId" :sourceid="$route.params.id"
                                    :modulename="$route.params.moduleName" :submodulecode="$route.params.subModuleCode"
                                    v-bind:listtype="NoteListType" :showNotePopUp="isShowNotePopup"
                                    callfrom="quickViewAndNoteJS"></crmnote>
                            </div>
                            <div class="tab-pane fade p-0 pb-2 border-0" id="tab-attachment-content" role="tabpanel"
                                aria-labelledby="tab-attachment-content" :class="{ 'active show': activeKey == 3 }">
                                <crmattachment v-bind:attachmentdata="attachmentData" :sourceid="$route.params.id">
                                </crmattachment>
                            </div>
                            <div class="tab-pane fade p-0 border-0" id="tab-tax-content" role="tabpanel"
                                aria-labelledby="tab-tax-content" :class="{ 'active show': activeKey == 4 }">
                                <taxdata></taxdata>
                            </div>
                        </div>
                    </div>
            </div>           
        </section>
        <modalattachment v-if="showAttachmentModal" @close="showAttachmentModal = false" :userid="userid"
            :companyid="companyId" :id="$route.params.id" :modulename="$route.params.moduleName"
            :submodulecode="$route.params.subModuleCode" :containername="containername" imagesizedata="">
            <h5 slot="header" class="modal-title">{{$t('Attachments')}}</h5>
        </modalattachment>
    </section>
</template>
<script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import taxdata from "../../../components/Common/Modal/taxdata.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import $ from "jquery";
export default {
    components: {
        crmtimeline,
        crmnote,
        crmattachment,
        taxdata,
        modalattachment,
        crmreadmore
    },
    data() {
        return {
            activeKey: 1,
            isNotes: false,
            isTax: false,
            isAttachment: false,
            iscontactDetails: null,
            LeftViewData: [],
            RightViewData: null,
            showAttachmentModal: false,
            attachmentData: [],
            activitiesData: null,
            productData: null,
            noteData: [],
            productHeaders: null,
            contactsData: null,
            contactsHeader: null,
            timelinedata: [],
            priceHeader: null,
            priceData: null,
            noRecordForTab: false,
            isShowNoteDialogue: null,
            showPriceBookModal: null,
            showtaxModal: false,
            imagePath: '',
            itemTypeName: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
            currentTab: "",
            NoteListType: 'list',
            userid: null,
            companyId: null,
            containername: null,
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            maxsize:
                "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
            disallowedfiletypes:
                "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize'))",
            isShowNotePopup: false,
            isLoading:false
        }
    },
    created: function () {
        this.userid= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.containername= this.GetUserInfo.ContainerName;
        this.FetchData();
    },
    updated: function () {
        var vm = this;
        setTimeout(function () {
            vm.setHeaderForSelectedTab();
        }, 500);
        $("#ancActivityExpandAllCollapseAll").click(function () {
            if ($(this).attr("show") == "0") 
    {$(this).attr("show", "1");
     $("#divconversationbox").find("#accordion").find('.collapse').addClass('show'); }
     else { 
        $(this).attr("show", "0"); 
     $("#divconversationbox").find("#accordion").find('.collapse').removeClass('show')
    }
        });
    },
    methods: {
        DisableButton: function () {
            var vm = this;
            vm.isNotes = false;
            vm.isAttachment = false;
        },
        GetLocalizedValue: function (fieldName, displayName) {            
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        openShowNotePopup: function () {
            var ths = this;
            if (
                typeof ths.checkForAdditionalPermission !== "undefined" &&
                !ths.checkForAdditionalPermission("AddNote")
            ) {
                vm.ShowAlert(
                    vm.$t("additionalOwnerAuthorityWarning"),
                    "warning",
                    true,
                    vm.$t("Alert")
                );
            } else {
                ths.isShowNotePopup = true;
                $("#tab-notes").trigger("click");
                ths.loadData("notes");
            }
        },
        checkPrivilegeDetailsForOwner: function (e, item) {
            if (GetPrivilege("@Url.Action(AddAdditionalowner, CRM)", true, false)) {
                this.openAddAdditionalOwner(null);
            } else {
                e.preventDefault();
            }
        }, 
        FilterDDLandNormalValue: function (item) {
            if (item && item.length > 0) {
                try {
                    return JSON.parse(item)[0].name;
                } catch (ex) {
                    return "";
                }
            } else {
                return "";
            }
        },
        openCRMAttachmentModal: function () {

            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddAttachment")) {
                alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {

                this.showAttachmentModal = (this.showAttachmentModal) ? false : true;
            }
        },
        FilterDDLValueForID: function (item) {
            if (item.length > 0) {
                try {
                    if (typeof JSON.parse(item)[0] == "object") {
                        return JSON.parse(item)[0].value;
                    } else {
                        return JSON.parse(item)[0];
                    }
                } catch (ex) {
                    return "";
                }
            } else {
                return "";
            }
        },
        FetchData: function () {
            var vm = this;
             vm.isLoading==true;
            LeadDataService.FormLayoutView(vm.ApiURL).then(function (response) {                
                var fullLeftJSON = response.data.LEFTDATA;
                vm.timelinedata = [];
                fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                    return element.form_field_visibility == 'YES';
                });
                vm.imagePath = response.data.IMAGEPATH;
                if (
                    response.data.RIGHTDATA != null &&
                    response.data.RIGHTDATA.length > 0
                ) {
                    var fullJSON = response.data.RIGHTDATA;
                    var gruopBy = "";
                    $.each(
                        fullJSON,
                        $.proxy(function (index, item) {
                            gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                            var chkGroup = $.grep(
                                this.timelinedata,
                                function (element, index) {
                                    return element.groupKey == gruopBy;
                                }
                            );
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullJSON, function (element, index) {
                                    return (
                                        vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy
                                    );
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        groupKey: gruopBy,
                                        groupData: returnedData,
                                    };
                                    vm.timelinedata.push(objGroup);
                                }
                            }
                        }, vm)
                    );
                    vm.isLoading=false;
                    vm.noRecordForTab = false;
                } else {
                    vm.noRecordForTab = true;
                }
                if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                    var groupId = fullLeftJSON[0].group_id;
                    $.each(fullLeftJSON, $.proxy(function (index, item) {
                       
                        if (item.name == 'asset_type') {
                            vm.itemTypeName = item.value;
                        }
                        var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    LeftViewData: returnedData
                                };
                                vm.LeftViewData.push(objGroup);
                            }
                        }
                        groupId = item.group_id;
                    }, vm));
                }
                setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
            })
        },
        changeDateForAddNoteUsrEnd: function (value) {
            try {
                var timeFormat = Globalize.culture().calendar.patterns.t;
                timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                if (userTimeZone[0].indexOf('+') >= 0) {
                    getDate = new Date(getDate.setHours(getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))));
                    getDate = new Date(getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1])));
                } else if (userTimeZone[0].indexOf('-') >= 0) {
                    getDate = new Date(getDate.setHours(getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))));
                    getDate = new Date(getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1])));
                }
                return moment(getDate, "YYYY-MM-DD HH:mm").format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
            } catch (e) {
                var timeFormat = Globalize.culture().calendar.patterns.t;
                timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                return moment(String(value)).format(String(Globalize.culture().calendar.patterns.d).toUpperCase() + " " + String(timeFormat));
            }
        },
        openNoteDialogue() {
            this.isShowNoteDialogue = (this.isShowNoteDialogue) ? false : true;
        },
        openPriceBookModel(priceBookDetails) {
        
            this.iscontactDetails = priceBookDetails
            this.showPriceBookModal = (this.showPriceBookModal) ? false : true;
        },
        loadData: function (tabName, callFrom) {
    
            var vm = this;
            vm.currentTab = tabName;
            vm.DisableButton();
            if (tabName == "attachments") {
                vm.isAttachment = true;
                vm.activeKey = 3;
            }
            else if (tabName == "notes") {
                vm.isNotes = true;
                vm.activeKey = 2;
                if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
            }
            else if(tabName == "tax"){
               
                vm.isTax = true;
                vm.activeKey = 4;
            }
            if (tabName != "" && tabName != null) {
                 

                if (tabName == "attachments") {
                    LeadDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                         
                        if (response.data != null) {
                            vm.attachmentData = response.data.data;
                            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }

                else if (tabName == "timeline") {
                    vm.activeKey = 1;
                    vm.isDetailPage = true;
                    LeadDataService.TimelineListing(vm.ApiURL).then(function (response) {
                         
                        if (response.data != null) {
                            vm.timelinedata = [];
                            var fullJSON = response.data.data;
                            var gruopBy = "";
                            $.each(
                                fullJSON,
                                $.proxy(function (index, item) {
                                    gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                                    var chkGroup = $.grep(
                                        vm.timelinedata,
                                        function (element, index) {
                                            return element.groupKey == gruopBy;
                                        }
                                    );
                                    if (chkGroup.length == 0) {
                                        var returnedData = $.grep(
                                            fullJSON,
                                            function (element, index) {
                                                return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                            }
                                        );
                                        if (returnedData.length > 0) {
                                            var objGroup = {
                                                groupKey: gruopBy,
                                                groupData: returnedData,
                                            };
                                            vm.timelinedata.push(objGroup);
                                        }
                                    }
                                }, vm));
                            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }

                else if (tabName == "notes") {
                    vm.noteData = [];
                    LeadDataService.NoteListing(vm.ApiURL).then(function (response) {
                    if (response.data != null && response.data.data.length > 0) {
                            vm.noteData = response.data.data;
                            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                            setTimeout(function(){vm.setHeaderForSelectedTab()}, 500);
                            setTimeout(function(){vm.bindLeftTabEventForSetHeading()}, 500);
                            vm.noRecordForTab = false;
                        } else {
                            setTimeout(function(){vm.setHeaderForSelectedTab()}, 500);
                            setTimeout(function(){vm.bindLeftTabEventForSetHeading()}, 500);
                            vm.noRecordForTab = true;
                        }
                    });
                }

                else {
                     
                }
            }
        }
    }
}
</script>