<template>
    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 ">
        <Form ref="form" autocomplete="off">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 border border-grey mb-2"
            v-for="(taxDataitem, taxDataIndex) in taxData" :key="taxDataIndex">
            <div class="black-header ">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left py-2">
                    <div class="form-head">
                        <div class="heading-border">
                            <h5 class="h5" v-if="taxDataitem.forName=='country'">
                                <span> {{$t('CountryTax')}} </span>
                                <a href="javascript:;" class="text-dark formbtn float-right p-0 w-10 text-right"
                                    v-if="taxDataitem.forName=='country'" v-on:click="AddMoreTax('0',taxDataIndex)"><em    class="fa fa-plus-circle" style="font-size:26px;"></em></a>
                            </h5>
                            <h5 class="h5" v-if="taxDataitem.forName=='state'">
                                <span> {{$t('StateTax')}} </span>
                                <a href="javascript:;" class="text-dark formbtn float-right p-0 w-10 text-right"
                                    v-if="taxDataitem.forName=='state'" v-on:click="AddMoreTax('1',taxDataIndex)"><em    class="fa fa-plus-circle" style="font-size:26px;"></em></a>
                            </h5>
                            <h5 class="h5" v-if="taxDataitem.forName=='district'">
                                <span> {{('CountyTax')}} </span>
                                <a href="javascript:;" class="text-dark formbtn float-right p-0 w-10 text-right"
                                    v-if="taxDataitem.forName=='district'" v-on:click="AddMoreTax('2',taxDataIndex)"><em    class="fa fa-plus-circle" style="font-size:26px;"></em></a>
                            </h5>
                            <h5 class="h5" v-if="taxDataitem.forName=='zipcode'">
                                <span> {{$t('ZipcodeTax')}} </span>
                                <a href="javascript:;" class="text-dark formbtn float-right p-0 w-10 text-right"
                                    v-if="taxDataitem.forName=='zipcode'" v-on:click="AddMoreTax('3',taxDataIndex)"><em    class="fa fa-plus-circle" style="font-size:26px;"></em></a>
                            </h5>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-12 float-left p-0 py-2 mb-2 highlighter">                
                    <div class="col-12 float-left mt-2 p-0 bg_highlighter"
                        v-for="(txtforItem, txtforIndex) in taxDataitem.taxFor" :key="txtforIndex">
                        <div class="col-12 float-left my-0 p-0">
                            <div class="col-11 float-left p-0">
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left">
                                    <Field name="countryname" :rules="{'required':false}" label="countryname"
                                    v-slot="{errors,touched,invalid,changed}">
                                        <Multiselect v-model="txtforItem.countryId" :placeholder="$t('SelectCountry')" noOptionsText=""
                                            :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0"
                                            :searchable="true" track-by="value"  @update:modelValue="changed" label="label"
                                            :options="CountryData"  @search-change="onSearch($event,txtforItem,taxDataIndex,txtforIndex,'search')">
                                        </Multiselect>
                                        <span   name="countryname" class="invalid-feedback d-block">{{errors[0]}}</span>
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left mb-2 float-left"
                                    v-if="txtforItem.taxForName=='state'">
                                    <Field name="statename" :rules="{'required':false}" label="statename"
                                        v-slot="{errors,touched,invalid,changed}">
                                    <Multiselect v-model="txtforItem.stateId" :placeholder="$t('SelectStateName')"  noOptionsText=""
                                        :filter-results="false" :min-chars="1" :resolve-on-load="false" :delay="0"
                                        :searchable="true" track-by="value"  @update:modelValue="changed"  label="label"
                                        :options="txtforItem.taxForOption"  @search-change="onSearch($event,txtforItem,taxDataIndex,txtforIndex,'search')"></Multiselect>
                                        <span   name="statename" class="invalid-feedback d-block">{{errors[0]}}</span>
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left mb-2 float-left"
                                    v-if="txtforItem.taxForName=='district'">
                                    <Field name="districtname" :rules="{'required':false}" label="districtname"
                                        v-slot="{errors,touched,invalid,changed}">
                                    <Multiselect v-model="txtforItem.districtId" :filter-results="false" :min-chars="1" label="label"
                                        :resolve-on-load="false" :delay="0"  noOptionsText=""  :searchable="true" track-by="value"  @update:modelValue="changed"
                                        :options="txtforItem.taxForOption"  @search-change="onSearch($event,txtforItem,taxDataIndex,txtforIndex,'search')"></Multiselect>
                                        <span   name="districtname" class="invalid-feedback d-block">{{errors[0]}}</span>
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left mb-2 float-left"
                                    v-if="txtforItem.taxForName=='zipcode'">
                                    <Field name="zipCode" :rules="{'ZipCode':true}" label="zipCode"
                                        v-slot="{errors,touched,invalid,changed}">
                                    <Multiselect v-model="txtforItem.zipCodeId" :filter-results="false" :min-chars="1"  noOptionsText="" label="label"
                                        :resolve-on-load="false" :delay="0" :searchable="true" track-by="value"  @update:modelValue="changed"
                                        :options="txtforItem.taxForOption"  @search-change="onSearch($event,txtforItem,taxDataIndex,txtforIndex,'search')"></Multiselect>
                                        <span   name="zipCode" class="invalid-feedback d-block">{{errors[0]}}</span>
                                    </Field>
                                </div>
                            </div>
                            <div class="col-1 float-right p-0">
                                <a href="javascript:;" class="btn formbtn "
                                    v-on:click="RemoveTaxForRow(txtforItem,taxDataIndex,txtforIndex)"
                                    v-if="txtforIndex>0">
                                    <em class="fa fa-trash text-danger" style="font-size:23px;"></em>
                                    <span class="ml-1 text-dark" style="font-size:14px;"></span>
                                </a>
                            </div>
                        </div>
                        <div class="col-12 float-left mt-2 p-0" v-for="(taxitem, taxItemIndex) in txtforItem.taxItem"
                            :key="taxItemIndex">
                            <div class="col-11 float-left p-0">
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left mb-2">
                                    <Field :name="'taxName'+taxDataIndex+txtforIndex+taxItemIndex" :rules="{'required':false}" label="taxName" >
                                    <input  class="form-control" v-model="taxitem.taxName"  v-bind:name="'taxName'+taxDataIndex+txtforIndex+taxItemIndex" />
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left mb-2">
                                    <Field name="taxType" :rules="{'required':false}" label="taxType" v-model="taxitem.taxType" >
                                    <select  class="form-control" v-on:change="CheckProbability(taxitem)"
                                        >
                                        <option value="">{{$t('SelectTaxType')}}</option>
                                        <option value="percent">{{$t('Percent')}}</option>
                                        <option value="flat">{{$t('Flat')}}</option>
                                    </select>
                                    </Field>
                                </div>
                                <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left mb-2">
                                    <Field :name="'taxValue'+taxDataIndex+txtforIndex+taxItemIndex" :rules="{'required':true}" label="Tax type" v-slot="{errors,touched,invalid}">
                                    <input v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}"
                                        v-model.number="taxitem.taxValue"
                                        v-bind:name="'taxValue'+taxDataIndex+txtforIndex+taxItemIndex"
                                        v-on:keypress="isNumber($event)" v-on:keyup="CheckProbability(taxitem)"
                                        />
                                        <span   :name="'taxValue'+taxDataIndex+txtforIndex+taxItemIndex" class="invalid-feedback">{{errors[0]}}</span>
                                    </Field>                                  
                                </div>
                            </div>
                            <div class="col-1 float-left p-0">
                                <a href="javascript:;" class="btn formbtn"
                                    v-on:click="RemoveTaxRow(taxitem,taxDataIndex,txtforIndex)"
                                    :title="$t('DeleteLineItem')" v-if="taxItemIndex>0">
                                    <em class="fa fa-trash text-danger" style="font-size:23px;"></em>
                                </a>
                                <a href="javascript:;" class="btn formbtn text-success" :title="$t('AddMoreLineItem')"
                                    v-on:click="AddItem(taxDataIndex,txtforIndex)" v-if="taxItemIndex==0">
                                    <em class="fa fa-plus text-success" style="font-size:23px;"></em>
                                </a>
                            </div>
                        </div>
                    </div>
                
            </div>
        </div>
        <div class="col-12 float-right text-right py-1 px-0">
            <a href="javascript:;" :title="$t('Save')" class="btn btn-success mr-2" v-on:click="SaveTax()"><em class="fa fa-save pr-2"></em> {{$t('Save')}}</a>
            <RouterLink :to="'/CRM/ItemTypeListing'" :title="$t('Cancel')"  class="btn btn-danger"><em class="fa fa-times"></em> {{$t('Cancel')}}</RouterLink>
        </div>
    </Form>
    </div>
</template>
<script>
import ProductTypeDataService from "../../../services/DataService";
import { RouterLink } from "vue-router";
import $ from "jquery";
export default {
    components: { RouterLink },
    data() {
        return {
            CountryData: [],
            taxData: [],
            taxGroupData: []
        };
    },
    created: function () {
        var VueObj = this;
        VueObj.ShowSearchData("", "COUNTRIES");
        VueObj.FetchData();
    },
    methods: {
        map(obj) {
            var result = {};
            Object.keys(obj).forEach(function (key) {
                switch (key) {
                    case "name":
                        result["label"] = obj[key];
                        break;
                    case "value":
                        result["value"] = obj[key];
                        break;
                }
            });
            return result;
        },
        CheckProbability: function (item) {
            if (item.taxType == "percent" && item.taxType != "") {
                if (item.taxValue > 100) {
                    item.taxValue = "";
                    this.ShowAlert(this.$t("Tax percentage can not be more than 100."), "warning", true, this.$t("Alert"));
                    return false;
                }
            }
        },
        isNumber: function (evt) {
           var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();
            }
            else {
                return true;
            }
        },
        onSearch: function onSearch(search, item, taxDataIndex, taxForIndex, isFor) {
            var vm = this;
            var type = item.taxForName;
            var refId = item.countryId.value;
            if (isFor != "search") {
                refId = item.value;
            }
            if (typeof (refId) != "undefined") {
                var param = `id=&moduleName=${vm.$route.params.moduleName}&type=${type}&refId=${refId}`;
                param += "&search=" + escape(search);
                ProductTypeDataService.getDDLData(param).then(function (response) {
                    var json = response.data;
                    if (json) {
                        if (isFor == "search") {
                            //item.countryId = refId;
                            item.taxForOption = json.DATA.map((item) => {
                                return { value: item.value, label: item.name };
                            });
                        }
                        else {
                            vm.taxData[taxDataIndex].taxFor[taxForIndex].taxForOption = json.DATA;
                            if (isFor != "search") {
                                vm.taxData[taxDataIndex].taxFor[taxForIndex].countryId = val;
                            }
                        }
                    }
                    else {
                        vm.taxData[taxDataIndex].taxFor[taxForIndex].taxForOption = [];
                        if (isFor != "search") {
                            vm.taxData[taxDataIndex].taxFor[taxForIndex].countryId = val;
                        }
                    }
                });
            }
        },
        setSelectedValue(item, val, taxDataIndex, taxForIndex, search, isFor) {
            var refId = val;
            var vm = this;
            if (isFor != "search") {
                refId = val.value;
            }
            if (typeof (refId) != "undefined") {
             
                var url = `id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&type=${vm.type}&refId=${vm.refId}`;
                ProductTypeDataService.GetDDLData(url + "&search=" + escape(search)).then(function (response) {
                    var json = response.data;
                    if (json != null) {
                        if (isFor == "search") {
                            item.taxForOption = json.DATA
                            return item.taxForOption;
                        }
                        else {
                            vm.taxData[taxDataIndex].taxFor[taxForIndex].taxForOption = json.DATA;
                            if (isFor != "search") {
                                vm.taxData[taxDataIndex].taxFor[taxForIndex].countryId = val;
                            }
                        }
                    }
                    else {
                        vm.taxData[taxDataIndex].taxFor[taxForIndex].taxForOption = [];
                        if (isFor != "search") {
                            vm.taxData[taxDataIndex].taxFor[taxForIndex].countryId = val;
                        }
                    }
                });
            }
        },
        getSelected: function (item, val) {
            if (Array.isArray(val)) {
                item.value = val;
            }
            if (typeof (val) === "object") {
                if (item.taxForName == "state") {
                    item.stateId = val;
                }
                else if (item.taxForName == "district") {
                    item.districtId = val;
                }
                else if (item.taxForName == "zipcode") {
                    item.zipCodeId = val;
                }
            }
        },
        RemoveTaxRow: function (childItem, taxDataIndex, taxForIndex) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteField"), true, null, function (result) {
                vm.taxData[taxDataIndex].taxFor[taxForIndex].taxItem.splice(childItem, 1);
            });
        },
        RemoveTaxForRow: function (item, taxDataIndex, taxForIndex) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteField"), true, null, function (result) {
                vm.taxData[taxDataIndex].taxFor.splice(item, 1);
            });
        },
        ShowSearchData(search, type) {
            var vm = this;
            var url = `id=&moduleName=${vm.$route.params.moduleName}&type=${type}`;
            ProductTypeDataService.getDDLData(url + "&search=" + escape(search)).then(function (response) {
                var json = response.data;
                if (json != null) {
                    vm.CountryData = json.DATA.map((item) => {
                        return { value: item.value, label: item.name };
                    });
                }
            });
        },
        MakeNormalArray: function (value) {
            if (value) {
                try {
                    return JSON.parse(value);
                }
                catch (ex) {
                    return value;
                }
            }
            else {
                value = [];
            }
        },
        FetchData: function () {
            var taxForArray = [
                { name: "country" },
                { name: "state" },
                { name: "district" },
                { name: "zipcode" }
            ];
            var vm = this;
            var url = `id=${vm.$route.params.id}`;
            ProductTypeDataService.GetProductTypeTax(url)
                .then(function (response) {
                if (response.data != null) {
                    var fullData = response.data.DATA;
                    if (fullData != null) {
                        for (var item of taxForArray) {
                            var taxForName = item.name;
                            var chkGroup = $.grep(fullData, function (element, index) {
                                return element.TAX_FOR == taxForName; //&& element.TAX_COUNTRY == chkId;
                            });
                            if (chkGroup.length > 0) {
                                //chkGroup = chkGroup[0];
                                var taxForItems = [];
                                var chktaxFor = "";
                                var chktaxItemGroup = "";
                                $.each(chkGroup, function (taxForIndex, taxForItem) {
                                    var chktaxItem = [];
                                    var taxItems = [];
                                    if (taxForName == "country") {
                                        chktaxFor = taxForItem.TAX_COUNTRY;
                                        chktaxItem = $.grep(fullData, function (element, index) {
                                            return element.TAX_FOR == taxForName && element.TAX_COUNTRY == taxForItem.TAX_COUNTRY;
                                        });
                                    }
                                    else if (taxForName == "state") {
                                        chktaxFor = taxForItem.TAX_STATE;
                                        chktaxItem = $.grep(fullData, function (element, index) {
                                            return element.TAX_FOR == taxForName && element.TAX_STATE == taxForItem.TAX_STATE;
                                        });
                                    }
                                    else if (taxForName == "district") {
                                        chktaxFor = taxForItem.TAX_DISTRICT;
                                        chktaxItem = $.grep(fullData, function (element, index) {
                                            return element.TAX_FOR == taxForName && element.TAX_DISTRICT == taxForItem.TAX_DISTRICT;
                                        });
                                    }
                                    else if (taxForName == "zipcode") {
                                        chktaxFor = taxForItem.TAX_ZIPCODE;
                                        chktaxItem = $.grep(fullData, function (element, index) {
                                            return element.TAX_FOR == taxForName && element.TAX_ZIPCODE == taxForItem.TAX_ZIPCODE;
                                        });
                                    }
                                    if (chktaxItem.length > 0) {
                                        taxItems = [];
                                        if (chktaxFor != chktaxItemGroup) {
                                            $.each(chktaxItem, function (taxItemIndex, taxItem) {
                                                var obj = {
                                                    taxName: taxItem.TAX_NAME,
                                                    taxType: taxItem.TAX_TYPE,
                                                    taxValue: taxItem.TAX_RATE,
                                                };
                                                taxItems.push(obj);
                                            });
                                            var taxForOptions = [];
                                            var countryOptions = [];
                                            if (chktaxItem[0].TAX_COUNTRY_OPTIONS != "") {
                                                countryOptions = vm.MakeNormalArray(chktaxItem[0].TAX_COUNTRY_OPTIONS);
                                            }
                                            if (chktaxItem[0].TAX_ZIPCODE_OPTIONS != "" && taxForName == "zipcode") {
                                                taxForOptions = vm.MakeNormalArray(chktaxItem[0].TAX_ZIPCODE_OPTIONS);
                                            }
                                            else if (chktaxItem[0].TAX_DISTRICT_OPTIONS != "" && taxForName == "district") {
                                                taxForOptions = vm.MakeNormalArray(chktaxItem[0].TAX_DISTRICT_OPTIONS);
                                            }
                                            else if (chktaxItem[0].TAX_STATE_OPTIONS != "" && taxForName == "state") {
                                                taxForOptions = vm.MakeNormalArray(chktaxItem[0].TAX_STATE_OPTIONS);
                                            }
                                            if (taxForOptions != null && taxForOptions.length > 0) {
                                                var taxFor = { taxForName: taxForName, countryId: countryOptions, stateId: taxForOptions, districtId: taxForOptions, zipCodeId: taxForOptions, taxForOption: taxForOptions, taxItem: taxItems };
                                                taxForItems.push(taxFor);
                                            }
                                            else {
                                                var taxFor = { taxForName: taxForName, countryId: countryOptions, stateId: chktaxItem[0].TAX_STATE, districtId: chktaxItem[0].TAX_DISTRICT, zipCodeId: chktaxItem[0].TAX_ZIPCODE, taxForOption: taxForOptions, taxItem: taxItems };
                                                taxForItems.push(taxFor);
                                            }
                                        }
                                        if (taxForName == "country") {
                                            chktaxItemGroup = chktaxItem[0].TAX_COUNTRY;
                                        }
                                        else if (taxForName == "state") {
                                            chktaxItemGroup = chktaxItem[0].TAX_STATE;
                                        }
                                        else if (taxForName == "district") {
                                            chktaxItemGroup = chktaxItem[0].TAX_DISTRICT;
                                        }
                                        else if (taxForName == "zipcode") {
                                            chktaxItemGroup = chktaxItem[0].TAX_ZIPCODE;
                                        }
                                    }
                                    else {
                                        taxItems = [{ taxName: "", taxType: "", taxValue: "" }];
                                        var taxFor = { taxForName: taxForName, countryId: taxForItem.TAX_COUNTRY, stateId: taxForItem.TAX_STATE, districtId: taxForItem.TAX_DISTRICT, zipCodeId: taxForItem.TAX_ZIPCODE, taxForOption: [], taxItem: taxItems };
                                        taxForItems.push(taxFor);
                                    }
                                });
                                var object = {
                                    forName: taxForName,
                                    taxFor: taxForItems,
                                };
                                vm.taxData.push(object);
                            }
                            else {
                                vm.RenderItem(taxForName);
                            }
                        }
                    }
                    else {
                        vm.RenderEmptyData(taxForArray);
                    }
                }
                else {
                    vm.RenderEmptyData(taxForArray);
                }
            });
        },
        RenderEmptyData: function (taxForArray) {
            for (var item of taxForArray) {
                this.RenderItem(item.name);
            }
        },
        RenderItem: function (taxForName) {
            var taxItem = [{ taxName: "", taxType: "", taxValue: "" }];
            var taxFor = [{ taxForName: taxForName, countryId: "", stateId: "", districtId: "", zipCodeId: "", taxForOption: [], taxItem: taxItem }];
            var object = {
                forName: taxForName,
                taxFor: taxFor,
            };
            this.taxData.push(object);
        },
        AddItem: function (taxDataIndex, taxForIndex) {
            var taxItem = { taxName: "", taxType: "", taxValue: "" };
            this.taxData[taxDataIndex].taxFor[taxForIndex].taxItem.push(taxItem);
        },
        AddMoreTax: function (type, taxDataIndex) {
            var taxForArray = [
                { name: "country" },
                { name: "state" },
                { name: "district" },
                { name: "zipcode" }
            ];
            var taxItem = [{ taxName: "", taxType: "", taxValue: "" }];
            var taxFor = { taxForName: taxForArray[type].name, countryId: "", stateId: "", districtId: "", zipCodeId: "", taxForOption: [], taxItem: taxItem };
            this.taxData[taxDataIndex].taxFor.push(taxFor);
        },
        createString: function (array, key) {
            return array.map(function (obj) {
                return "" + obj[key] + "";
            }).join(",");
        },
        SaveTax: function () {
            this.$refs.form.validate().then((result) => {
                if (result) {
                    if (this.taxData.length > 0) {
                        var vm = this;
                        var postData = [];
                        $.each(this.taxData, $.proxy(function (key, taxDataItem) {
                            $.each(taxDataItem.taxFor, function (childKey, taxForItem) {
                                $.each(taxForItem.taxItem, function (childKey, taxItem) {
                                    var countryId = taxForItem.countryId;
                                    if (typeof taxForItem.countryId === "object" && !Array.isArray(taxForItem.countryId)) {
                                        countryId = taxForItem.countryId.value;
                                    }
                                    else if (Array.isArray(taxForItem.countryId)) {
                                        countryId = vm.createString(taxForItem.countryId, "value");
                                    }
                                    var stateId = taxForItem.stateId;
                                    if (Array.isArray(taxForItem.stateId)) {
                                        stateId = vm.createString(taxForItem.stateId, "value");
                                    }
                                    var districtId = taxForItem.districtId;
                                    if (Array.isArray(taxForItem.districtId)) {
                                        districtId = vm.createString(taxForItem.districtId, "value");
                                    }
                                    var zipCodeId = taxForItem.zipCodeId;
                                    if (Array.isArray(taxForItem.zipCodeId)) {
                                        zipCodeId = vm.createString(taxForItem.zipCodeId, "value");
                                    }
                                    if (countryId != "") {
                                        var obj = {
                                            name: taxItem.taxName,
                                            taxFor: taxForItem.taxForName,
                                            countryId: countryId,
                                            stateId: taxForItem.taxForName == "state" ? stateId : "",
                                            districtId: taxForItem.taxForName == "district" ? districtId : "",
                                            zipCodeId: taxForItem.taxForName == "zipcode" ? zipCodeId : "",
                                            taxType: taxItem.taxType,
                                            taxValue: taxItem.taxValue,
                                        };
                                        postData.push(obj);
                                    }
                                });
                            });
                        }, this));
                        var mainPostData = {
                            productTypeId: vm.$route.params.id,
                            taxData: postData
                        };
                        var objectPostString = JSON.stringify(mainPostData);
                        ProductTypeDataService.SaveTax(objectPostString).then(function (response) {
                            if (response.data[0].Status == "Success") {
                                if (postData.length == 0) {
                                    vm.ShowAlert(vm.$t("Select Atleast one type of tax."), "warning", true, vm.$t("Alert"));
                                    return false;
                                }
                                var box = vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Tax")]), "success", true, vm.$t("Alert"));
                                vm.$emit("close");
                                setTimeout(function () {
                                    box.modal("hide");
                                }, 1500);
                            }
                        });
                    }
                }
            });
        }
    },
    
}
</script>
